@import "../settings/colors";

.hexagon-menu {
  display: flex;
  align-items: center;

  & + .hexagon-menu {
    margin-top: -25px;

    @media (max-width: 768px) {
      margin-top: 0;
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.hex-item {
  position: absolute;
  top: 0;
  left: 50px;
  width: 80px;
  height: 153px;

  &:last-child {
    transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
    z-index: 1;
  }

  div {
    position: absolute;
    top: 0;
    width: 80px;
    height: 140px;
    transform-origin: center center;

    &::before,
    &::after {
      background-color: $color-primary;
      content: "";
      position: absolute;
      width: 100%;
      height: 3px;
      transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    &:nth-child(1) {
      transform: rotate(0deg);
    }

    &:nth-child(2) {
      transform: rotate(60deg);
    }

    &:nth-child(3) {
      transform: rotate(120deg);
    }
  }
}

.hexagon {
  position: relative;
  width: 130px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  & + .hexagon {
    margin-left: 16px;

    @media (max-width: 768px) {
      margin-left: 0;
    }
  }

  &:hover {
    z-index: 1;

    .hex-item div::before,
    .hex-item div::after {
      background-color: #ff0037;
    }

    .hex-item:first-child {
      transform: scale(1.2);
    }
  }
}

.hexagon-content {
  background-color: $color-primary;
  width: 124px;
  height: 70px;
  position: absolute;
  top: 41px;
  right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: $color-gray;
  z-index: 1;

  svg {
    font-size: 2em;

    + span {
      margin-top: 8px;
      color: $color-white;
      font-size: 12px;
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 0;
    border-left: 62px solid transparent;
    border-right: 62px solid transparent;
  }

  &::before {
    bottom: 100%;
    border-bottom: 36px solid $color-primary;
  }

  &::after {
    top: 100%;
    border-top: 36px solid $color-primary;
    left: 0;
  }
}

.hexagon-item {
  cursor: pointer;
  width: 200px;
  height: 153px;
  float: left;
  margin-left: -29px;
  z-index: 0;
  position: relative;
  transform: rotate(30deg);

  //&:first-child {
  //  margin-left: 0;
  //}
}
