@import "../settings/colors";

html,
body {
  height: 100%;
}

body {
  color: $body-color;
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  background: url("https://images.unsplash.com/photo-1465268218846-a984c79f1045?ixid=MXwxMjA3fDB8MHxzZWFyY2h8OHx8bGFuZCUyMHNjYXBlfGVufDB8fDB8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1600&q=60") no-repeat center center fixed;
  background-size: cover;

  h1 {
    visibility: hidden;
    width: 0;
    height: 0;
  }
}
